* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  text-decoration: none;

  padding: 0;
  margin: 0;
}

li {
  display: flex;
  margin-bottom: 5px;
}
p {
  margin-top: 5px;
  margin-bottom: 5px;
}

img {
  padding-right: 5px;
}
.bold {
  font-size: 32px;
  font-weight: 700;
}

.country-list {
  margin-bottom: 20px;
}
.country-info span {
  font-size: 18px;
  font-weight: 500;
}
